.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  position: relative;
  overflow: hidden;
}

.auth-background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><radialGradient id="a" cx="50%" cy="50%"><stop offset="0%" stop-color="%23ffffff" stop-opacity="0.1"/><stop offset="100%" stop-color="%23ffffff" stop-opacity="0"/></radialGradient></defs><circle cx="200" cy="200" r="100" fill="url(%23a)"/><circle cx="800" cy="300" r="150" fill="url(%23a)"/><circle cx="400" cy="700" r="120" fill="url(%23a)"/><circle cx="900" cy="800" r="80" fill="url(%23a)"/></svg>');
  background-size: cover;
}

.auth-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

.auth-content {
  position: relative;
  z-index: 10;
  width: 100%;
  max-width: 450px;
  padding: 20px;
}

.auth-card {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 24px;
  padding: 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Header */
.auth-header {
  text-align: center;
  margin-bottom: 30px;
}

.logo-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin-bottom: 16px;
}

.logo-icon {
  font-size: 2.5rem;
  color: var(--primary, #00c853);
  filter: drop-shadow(0 4px 8px rgba(0, 200, 83, 0.3));
}

.auth-header h1 {
  font-size: 2.5rem;
  font-weight: 800;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  margin: 0;
}

.auth-subtitle {
  color: #666;
  font-size: 1rem;
  margin: 0;
  font-weight: 500;
}

/* Demo Info */
.demo-info {
  background: linear-gradient(135deg, #00c853 0%, #00a047 100%);
  color: white;
  padding: 16px;
  border-radius: 12px;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  gap: 12px;
  box-shadow: 0 8px 20px rgba(0, 200, 83, 0.3);
}

.demo-icon {
  font-size: 1.2rem;
  flex-shrink: 0;
}

.demo-info div p {
  margin: 2px 0;
  font-size: 0.9rem;
}

.demo-info div p strong {
  font-weight: 600;
}

/* Messages */
.auth-message {
  padding: 12px 16px;
  border-radius: 8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  font-weight: 500;
}

.auth-message.error {
  background: rgba(255, 71, 87, 0.1);
  color: #ff4757;
  border: 1px solid rgba(255, 71, 87, 0.2);
}

.auth-message.success {
  background: rgba(0, 200, 83, 0.1);
  color: #00c853;
  border: 1px solid rgba(0, 200, 83, 0.2);
}

.message-icon {
  font-size: 1rem;
  flex-shrink: 0;
}

/* Form */
.auth-form {
  margin-bottom: 24px;
}

.form-group {
  margin-bottom: 20px;
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.input-icon {
  position: absolute;
  left: 16px;
  color: #999;
  font-size: 1.1rem;
  z-index: 2;
  transition: color 0.3s ease;
}

.input-wrapper input {
  width: 100%;
  padding: 16px 16px 16px 48px;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  font-size: 1rem;
  background: white;
  transition: all 0.3s ease;
  outline: none;
}

.input-wrapper input:focus {
  border-color: var(--primary, #00c853);
  box-shadow: 0 0 0 4px rgba(0, 200, 83, 0.1);
}

.input-wrapper input:focus + .input-icon {
  color: var(--primary, #00c853);
}

.input-wrapper input.error {
  border-color: #ff4757;
  box-shadow: 0 0 0 4px rgba(255, 71, 87, 0.1);
}

.password-toggle {
  position: absolute;
  right: 16px;
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.password-toggle:hover {
  color: var(--primary, #00c853);
  background: rgba(0, 200, 83, 0.1);
}

.error-text {
  color: #ff4757;
  font-size: 0.85rem;
  margin-top: 6px;
  display: block;
  font-weight: 500;
}

/* Submit Button */
.auth-submit-btn {
  width: 100%;
  padding: 16px;
  background: linear-gradient(135deg, var(--primary, #00c853) 0%, #00a047 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s ease;
  box-shadow: 0 8px 20px rgba(0, 200, 83, 0.3);
  position: relative;
  overflow: hidden;
}

.auth-submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 25px rgba(0, 200, 83, 0.4);
}

.auth-submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.btn-icon {
  font-size: 1rem;
  transition: transform 0.3s ease;
}

.auth-submit-btn:hover .btn-icon {
  transform: translateX(4px);
}

/* Loading Spinner */
.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Social Login */
.social-login {
  margin-bottom: 24px;
}

.divider {
  text-align: center;
  margin-bottom: 20px;
  position: relative;
}

.divider::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  height: 1px;
  background: #e1e5e9;
}

.divider span {
  background: rgba(255, 255, 255, 0.95);
  padding: 0 16px;
  color: #666;
  font-size: 0.9rem;
  font-weight: 500;
}

.social-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 12px;
}

.social-btn {
  padding: 12px;
  border: 2px solid #e1e5e9;
  border-radius: 12px;
  background: white;
  color: #666;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.social-btn:hover {
  border-color: var(--primary, #00c853);
  color: var(--primary, #00c853);
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.social-btn.google:hover {
  border-color: #db4437;
  color: #db4437;
}

.social-btn.facebook:hover {
  border-color: #4267B2;
  color: #4267B2;
}

.social-btn.apple:hover {
  border-color: #000;
  color: #000;
}

.social-btn svg {
  font-size: 1.2rem;
}

/* Toggle */
.auth-toggle {
  text-align: center;
}

.auth-toggle p {
  color: #666;
  font-size: 0.9rem;
  margin: 0;
}

.toggle-btn {
  background: none;
  border: none;
  color: var(--primary, #00c853);
  font-weight: 600;
  cursor: pointer;
  margin-left: 8px;
  text-decoration: underline;
  transition: all 0.3s ease;
}

.toggle-btn:hover {
  color: #00a047;
}

/* Responsive */
@media (max-width: 768px) {
  .auth-content {
    padding: 10px;
  }
  
  .auth-card {
    padding: 30px 20px;
  }
  
  .auth-header h1 {
    font-size: 2rem;
  }
  
  .logo-icon {
    font-size: 2rem;
  }
  
  .social-buttons {
    grid-template-columns: 1fr;
    gap: 8px;
  }
  
  .social-btn {
    flex-direction: row;
    justify-content: center;
    gap: 8px;
    padding: 14px;
  }
}

@media (max-width: 480px) {
  .auth-card {
    padding: 20px 16px;
  }
  
  .auth-header h1 {
    font-size: 1.8rem;
  }
  
  .input-wrapper input {
    padding: 14px 14px 14px 44px;
    font-size: 0.95rem;
  }
  
  .auth-submit-btn {
    padding: 14px;
    font-size: 1rem;
  }
}
